.user-container {
    padding: 16px 0;
    color: rgba(22, 24, 35, 0.75);
    font-weight: 600;
    font-size: 14px;
    position: relative;

    &.wrap {
        display: flex;
        flex-wrap: wrap;
        width: 100%;

        .title {
            width: 100%;
        }
    }


    &::after {
        content: "";
        position: absolute;
        left: 8px;
        right: 8px;
        top: 0;
        height: 1px;
        background: rgba(22, 24, 35, 0.12);
        transform: scaleY(0.5);
    }
}

.title {
    padding: 0 8px;
    margin-bottom: 8px;
}

.showMore {
    color: rgb(254, 44, 85);
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    padding: 0 8px;
    margin-top: 8px;
}

.link {
    padding-top: 8px;
}

.desc {
    padding: 0 8px;
    margin-bottom: 8px;
    color: rgba(22, 24, 35, 0.5);
    font-size: 16px;
    line-height: 22px;
    padding-bottom: 20px;
}