.item {
    padding: 20px 0;
    display: flex;
    align-items: flex-start;
    position: relative;
    scroll-snap-align: start;

    &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        transform: scaleY(0.5);
        background: rgba(22, 24, 35, 0.2);
    }
}

.item .avatar {
    width: 56px;
    height: 56px;
    border-radius: 50%;
}

.content {
    margin-left: 12px;
    width: 624px;
}

.text-info {
    margin-right: 114px;
    position: relative;
}

.text-info h2 {
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    display: inline-block;
    margin-right: 4px;
}

.text-info span {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    display: inline-block;
    line-height: 28px;
}

.music {
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    margin-top: 4px;
    margin-bottom: 12px;
}

.video-wrapper {
    display: flex;
    align-items: flex-end;
    flex-direction: row;

    .video {
        position: relative;
        background-size: cover;
        cursor: pointer;
        border-radius: 8px;
        overflow: hidden;
        margin-right: 20px;
        height: 491px;
        background-color: antiquewhite;
        width: 270px;

        &:hover .play-icon,
        &:hover .pause-icon,
        &:hover .volume-icon {
            opacity: 1;
        }

        video {
            object-fit: cover;
            display: block;
            width: 100%;
            height: 100%;


        }

        .play-icon,
        .pause-icon {
            left: 12px;
            position: absolute;
            bottom: 12px;
            padding: 10px;
            width: 40px;
            height: 40px;
            transition: opacity .3s;
            opacity: 0;
            bottom: 26px;
        }

        .volume-icon {
            right: 12px;
            position: absolute;
            bottom: 12px;
            padding: 8px;
            width: 40px;
            height: 40px;
            transition: opacity .3s;
            opacity: 0;
            bottom: 26px;
        }
    }
}

.icon {
    padding: 0;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;

    span {
        background-color: rgba(22, 24, 35, 0.06);
        width: 48px;
        height: 48px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 8px;
        margin-bottom: 6px;

    }

    strong {
        color: rgba(22, 24, 35, 0.75);
        font-size: 12px;
        line-height: 17px;
        text-align: center;
        font-weight: 600;
    }
}

.follow {
    position: absolute;
    right: 0;
    top: 28px;
    padding: 0 10px;
    min-width: 88px;
    line-height: 26px;
}