.container {
    display: flex;

    .content {
        display: flex;
        flex-direction: column;
        flex: 1 1 auto;
        margin: 0 auto;
        padding: 32px 24px;
        min-height: calc(100vh - 59px);
    }

    .content .inner-content {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;

        .header-content {
            position: relative;
            flex: 0 0 auto;
            margin-bottom: 20px;
            padding-right: 92px;
            max-width: 624px;

            .share-info {
                display: flex;
                align-items: center;

                img {
                    width: 116px;
                    height: 116px;
                    border-radius: 50%;
                }

                .title {
                    flex: 1;
                    cursor: pointer;
                    overflow: visible;
                    margin-left: 20px;
                    position: relative;

                    .icon {
                        color: rgb(32, 213, 236);
                        font-size: 20px;
                        padding-left: 8px;
                        position: absolute;
                        top: 10px
                    }

                    h2 {
                        font-weight: 700;
                        font-size: 32px;
                        line-height: 38px;
                        padding-bottom: 4px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        text-align: left;
                    }

                    h1 {
                        font-weight: 600;
                        font-size: 18px;
                        line-height: 25px;
                        text-overflow: ellipsis;
                        height: 25px;
                        overflow: hidden;
                        max-width: 450px;
                        white-space: nowrap;
                        display: flex;
                        align-items: flex-end;
                    }

                    .button {
                        font-weight: 600;
                        font-size: 18px;
                        line-height: 25px;
                        position: relative;
                        width: 208px;
                        height: 36px;
                        padding: 0;
                        margin: 0;
                        margin-top: 16px;

                        .message {
                            width: 164px;
                        }

                        .follow {
                            width: 208px;
                        }
                    }
                }
            }

            .count-info {
                display: flex;
                align-items: center;
                margin-top: 22px;
                color: rgba(18, 18, 18, .75);

                .div-number {
                    margin-right: 20px;
                    font-weight: 600;
                    font-size: 18px;
                    line-height: 25px;
                    display: flex;

                    span:nth-child(even) {
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 25px;
                        display: inline-block;
                        margin-left: 6px;
                    }

                    span:nth-child(odd) {
                        font-size: 18px;
                        line-height: 25px;
                    }
                }
            }

            .share_decs {
                font-weight: 400;
                font-size: 16px;
                line-height: 22px;
                color: rgba(18, 18, 18, 1.0);
                white-space: pre-line;
                margin-top: 10px;
            }

            .share-action {
                position: absolute;
                top: 11px;
                cursor: pointer;
                right: 52px;
            }

            .more-action {
                position: absolute;
                top: 11px;
                cursor: pointer;
                right: 12px;
            }

        }

        .body-content {
            display: flex;
            flex: 1 1 auto;
            flex-direction: column;
            justify-content: flex-start;
            min-height: 490px;
            min-width: 520px;
            align-items: flex-start;

            .video-tab {
                position: relative;
                display: flex;
                align-items: stretch;
                justify-content: stretch;
                height: 44px;
                width: 460px;
                margin-bottom: 8px;

                .videos-tab {
                    font-weight: 600;
                    font-size: 18px;
                    line-height: 25px;
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex: 1 1 0%;
                    height: 44px;
                    cursor: pointer;
                    text-align: center;
                }

                .liked-tab {
                    font-family: "IBM Plex Sans", SofiaPro, Arial, Tahoma, PingFangSC, sans-serif;
                    font-weight: 600;
                    font-size: 18px;
                    line-height: 25px;
                    position: relative;
                    display: flex;

                    align-items: center;

                    justify-content: center;
                    flex: 1 1 0%;
                    height: 44px;
                    cursor: pointer;
                    text-align: center;
                    color: rgb(18, 18, 18);
                }

                .bottom-line {
                    position: absolute;
                    width: 50%;
                    height: 2px;
                    background: rgb(22, 24, 35);
                    left: 0px;
                    bottom: 0px;
                    transition: transform 0.3s ease 0s;
                }

                &::before {
                    position: absolute;
                    bottom: 0px;
                    left: 0px;
                    content: "";
                    width: 100%;
                    height: 1px;
                    background-color: rgba(22, 24, 35, 0.2);
                    transform: scaleY(0.5);
                }
            }
        }
    }
}