.menu-items {
    width: 235px;

    .menu-item {
        width: 100%;
        justify-content: flex-start;
        white-space: nowrap;
        border-radius: 0;


    }
}

.menu-list {
    padding: 8px 0;
}

.spread {
    border-top: 1px solid rgba(22, 24, 35, 0.12);
}

.profile-popup {
    .item {
        padding: 4px 8px;

        &:hover {
            background: rgba(22, 24, 35, 0.03);
        }
    }
}