.container {
    position: absolute;
    z-index: 1002;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 300ms cubic-bezier(0.075, 0.82, 0.165, 1) 0s;
    height: 80%;
    background-color: rgb(255, 255, 255);
    border-radius: 8px;
    max-height: 693px;
    height: 80%;
    overflow: hidden;

    .close {
        transform: scale(1.7);
        position: absolute;
        top: 24px;
        border-radius: 50%;
        cursor: pointer;
        display: flex;
        -webkit-box-align: center;
        align-items: center;
        -webkit-box-pack: center;
        justify-content: center;
        z-index: 1;
        right: 24px;
        width: 24px;
        height: 24px;
        background: rgba(22, 24, 35, 0.03);
    }

    .content {
        width: 483px;
        height: 100%;
        padding: 48px 0px 0px;
        display: flex;
        flex-direction: column;
        display: flex;
        justify-content: space-between;

        .div-container {
            background-color: rgb(255, 255, 255);
            display: flex;
            justify-content: center;
            height: 64px;

            .wrapper {
                width: fit-content;
                display: flex;
                align-items: center;

                a {
                    color: rgb(254, 44, 85);
                }
            }
        }
    }

}

.list-login {
    h1 {
        color: rgb(22, 24, 35);
        font-size: 32px;
        margin: 16px auto;
        font-weight: bold;
        text-align: center;
    }

    .box-container {
        color: rgb(22, 24, 35);
        border: 1px solid rgba(22, 24, 35, 0.12);
        font-size: 15px;
        padding: 0px 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 44px;
        width: 375px;
        position: relative;
        cursor: pointer;
        margin-bottom: 16px;
        word-break: keep-all;
        white-space: nowrap;
        margin: 0 auto 16px;
    }



    .box-container .fb,
    .gg {
        display: inline-block;
        transform: scale(1.6);
        left: 12px;
        font-size: 20px;
        position: absolute;
        display: flex;
    }

}