.default {
    padding: 6px 8px;
    font-weight: 700;
    font-size: 1.6rem;
    line-height: 22px;
    border-radius: 4px;
    border: 1px solid transparent;
    width: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;

    +.default {
        margin-left: 8px;
    }

    +.default.nomg {
        margin-left: 0;
    }

}

.primary {
    background-color: rgba(254, 44, 85, 1.0);
    color: rgba(255, 255, 255, 1.0);

    &:hover {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), #FE2C55;
    }
}

.outline {
    color: rgba(254, 44, 85, 1.0);
    border-color: rgba(254, 44, 85, 1.0);
    background-color: rgba(255, 255, 255, 1.0);
    line-height: 25px;
}

.medium {
    width: 75%;
}

.large {
    width: 100%;
    padding: 12px 0;
}

.onlyWord {
    color: rgba(22, 24, 35, 0.5);
    font-weight: 600;
    font-size: 12px;
    line-height: 17px;
    margin-right: 6px;
    margin-top: 5px;
    display: inline-block;
    padding: 0;
}

.border {
    color: rgba(22, 24, 35, 0.75);
    font-weight: 700;
    font-size: 14px;
    box-shadow: 0 2px 8px rgb(0 0 0 / 6%);
    border-radius: 52px;
    text-align: center;
    white-space: nowrap;
    border-width: 1px;
    border-style: solid;
    border-color: rgba(22, 24, 35, 0.12);
    background-color: rgba(255, 255, 255, 1.0);
}

.rounded {
    border: 1px solid rgba(22, 24, 35, 0.12);
    border-radius: 2px;
    min-width: 110px;

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

.leftIcon {
    padding-left: 6px;
}

.title {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    flex-shrink: 300;
}

.pt {
    margin-top: 4px;
}

.noCenter {
    justify-content: flex-start;
}

.item {
    display: flex;
    box-sizing: border-box;
    width: fit-content;
    max-width: 100%;
    height: 24px;
    margin-bottom: 12px;
    padding: 3px 10px;
    border: 1px solid rgba(22, 24, 35, 0.2);
    border-radius: 12px;
    transition: background 200ms ease-in-out 0s;
    margin-right: 8px;

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

.avatar {
    padding: 0;
}

.large1 {
    width: 100%;
    line-height: 22px;
}