.wrapper {
    display: flex;
    padding: 9px 16px;
    cursor: pointer;
    align-items: center;


    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }

    &.userLink {
        padding: 8px;
        border-radius: 4px;

        .image {
            width: 32px;
            height: 32px;
        }

        .info {
            h4 {
                color: rgb(22, 24, 35);

                font-weight: 700;
                max-width: 260px;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
            }

            p {
                color: rgba(22, 24, 35, 0.75);
                font-size: 12px;
                line-height: 15px;
                max-width: 260px;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
            }


        }
    }
}

.image {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 12px;
}



.title {
    font-size: 16px;
    line-height: 22px;
    font-weight: 600;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.name {
    font-size: 14px;
    line-height: 20px;
    color: rgba(22, 24, 35, 0.5);
    font-weight: 400;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

}

.icon {
    margin-left: 4px;
    color: rgb(32, 213, 236);
}

.user {
    width: 320px;

    &-container {
        animation: all 1s linear;
        position: relative;
        padding: 20px;
        display: flex;
        flex-direction: column;

        .head-container {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 12px;

            img {
                width: 44px;
                height: 44px;
                border-radius: 50%;
            }

            .button {
                width: 106px;
            }
        }

        .user-title {
            color: rgb(22, 24, 35);
            font-family: "IBM Plex Sans", SofiaPro, Arial, Tahoma, PingFangSC, sans-serif;
            font-weight: 700;
            font-size: 18px;
            line-height: 25px;
        }

        .user-name {
            color: rgb(22, 24, 35);
            font-family: "IBM Plex Sans", ProximaNova, Arial, Tahoma, PingFangSC, sans-serif;
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
        }

        .user-follow {
            margin-top: 8px;
            display: flex;
            align-items: center;

            span:nth-child(odd) {
                font-weight: 700;
                margin-right: 6px;
                font-size: 17px;
                line-height: 20px;
                display: inline-block;
                color: black;
            }

            span:nth-child(even) {
                font-size: 17px;
                line-height: 20px;
                display: inline-block;
                margin-right: 12px;
            }
        }
    }
}