.tooltip {
    padding: 8px;
    background-color: rgba(84, 84, 84, 0.92);
    color: rgb(255, 255, 255);
    border-radius: 8px;
    font-weight: 600;
    position: relative;

    .triangle {
        position: absolute;
        top: -4.8px;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 0;
        height: 0;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 10px solid rgba(84, 84, 84, 0.92);
    }
}