.side-bar {
    width: 348px;
    padding: 2rem 0 0 0.8rem;
    position: relative;
    max-height: calc(100vh - 60px);
    overflow-y: overlay;
    background-color: white;
    overflow-x: hidden;

    &:hover+.scroll-bar {
        opacity: 1;
    }


    .wrapper {
        width: 100%;
        padding: 0.8rem;
        margin-left: 0;


        .nav-container {
            margin-bottom: 8px;


        }

        &:hover {
            background: rgba(22, 24, 35, 0.03);
        }

        h2 {
            font-weight: 700;
            font-size: 18px;
            line-height: 25px;
        }
    }

}

.scroll-bar {
    height: 274.017px;
    position: absolute;
    padding: 0 4px;
    top: 0;
    opacity: 0;
    right: 0;
    transition: opacity .5s;
    margin-right: -7px;

    .thumb {
        width: 10px;
        height: 100%;
        border-radius: 3px;
        background: rgba(22, 24, 35, .06);
    }
}

.text {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: rgba(22, 24, 35, 0.75);
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
}

.nav-container {
    margin-bottom: 8px;
}

.container {
    position: relative;
}

.popper {
    width: 3200px;
    height: 100px;
    position: absolute;
    z-index: 1000;
}

.user {
    width: 320px;

    &-container {
        animation: all 1s linear;
        position: relative;
        padding: 20px;
        display: flex;
        flex-direction: column;

        .head-container {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 12px;

            img {
                width: 44px;
                height: 44px;
                border-radius: 50%;
            }

            .button {
                width: 106px;
            }
        }

        .user-title {
            color: rgb(22, 24, 35);
            font-family: "IBM Plex Sans", SofiaPro, Arial, Tahoma, PingFangSC, sans-serif;
            font-weight: 700;
            font-size: 18px;
            line-height: 25px;
        }

        .user-name {
            color: rgb(22, 24, 35);
            font-family: "IBM Plex Sans", ProximaNova, Arial, Tahoma, PingFangSC, sans-serif;
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
        }

        .user-follow {
            margin-top: 8px;
            display: flex;
            align-items: center;

            span:nth-child(odd) {
                font-weight: 700;
                margin-right: 6px;
                font-size: 17px;
                line-height: 20px;
                display: inline-block;
                color: black;
            }

            span:nth-child(even) {
                font-size: 17px;
                line-height: 20px;
                display: inline-block;
                margin-right: 12px;
            }
        }
    }
}