.container {
    box-shadow: 0px 1px 1px rgb(0 0 0 / 12%);
    height: 60px;
    width: 100%;
    top: 0;
    position: fixed;
    z-index: 1000;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: white;
}

.wrapper {
    height: 100%;
    display: flex;
    align-items: center;
    width: 1150px;
    padding-right: 24px;
    padding-left: 24px;
    justify-content: space-between;
    position: relative;

    .logo {
        height: 42px;
    }

    &.onlyHeader {
        width: 100%;
    }
}

.right {
    min-width: 160px;
    display: flex;
    align-items: center;
    justify-content: center;

    .profile-icon {
        // background-image: url(../../../../assets/images/profile.jpeg);
        width: 32px;
        height: 32px;
        background: center no-repeat;
        background-size: cover;
        border-radius: 50%;
        cursor: pointer;
        margin-left: 20px;
    }
}

.icon {
    margin-left: 16px;
    padding: 3px 3px 0;
}

.button {
    justify-content: center;
}

.login-module {
    position: absolute;
    top: 400px;
    left: 50%;
}