.popper {
    width: 361px;

}

.title {
    height: 30px;
    padding: 5px 12px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    color: rgba(22, 24, 35, 0.5);
}

.form {
    display: flex;
    align-items: center;
    padding: 12px 16px;
    background: rgba(22, 24, 35, 0.06);
    border-radius: 92px;
    position: relative;
    height: 46px;
    width: 361px;
    border: 1px solid transparent;

    &:focus-within {
        border: 1px solid #cccdcf;
    }


    input {
        font-size: 16px;
        line-height: 22px;
        border: none;
        background: transparent;
        outline: none;
        padding: 0;
        width: 292px;
        caret-color: rgba(254, 44, 85, 1.0);
        appearance: textfield;


    }

    span {
        width: 1px;
        height: 28px;
        margin: -3px 0;
        background: rgba(22, 24, 35, 0.12);
    }

    .search {
        padding: 11px 16px 11px 12px;
        margin: -12px -16px;
        font-size: 0;
        cursor: pointer;
        outline: none;
        border: none;
        background: transparent;
        margin-left: 0;
        border-top-right-radius: 92px;
        border-bottom-right-radius: 92px;

        &:hover {
            background: rgba(22, 24, 35, 0.03);
        }

    }

}



.search-result {
    width: 353px;
}

.form-container {
    position: relative;
}